<script>
  import { fade } from "svelte/transition";
  export let caseTitle;
  export let caseRole;
  export let caseDescription;
  export let caseExecution;
  export let caseLink;
  export let caseImage;
  export let caseTech;
  export let orange;
  export let blue;
  export let red;
  export let green;
  export let purple;
</script>

<style>
  article {
    padding: 30px;
    box-shadow: 3px 3px 13px 13px rgba(0, 0, 0, 0.1);
    margin-bottom: 50px;
    background: #151515;
    background: linear-gradient(45deg, #151515, #222);
  }
  article:before {
    content: "";
    position: absolute;
    width: calc(100% - 65px);
    height: 4px;
    z-index: 9;
    margin-top: -30px;
    margin-left: -30px;
  }
  .orange:before {
    background: #aaa;
  }
  .blue:before {
    background: #aaa;
  }
  .green:before {
    background: #aaa;
  }
  .red:before {
    background: #aaa;
  }
  .purple:before {
    background: #aaa;
  }
  img {
    width: 100%;
    margin: 5px 0;
  }
  a,
  .nolink {
    float: right;
  }
  h4 {
    font-weight: bold;
  }
  @-webkit-keyframes gradient-slide {
    0% {
      background-position: 0% 54%;
    }
    50% {
      background-position: 100% 47%;
    }
    100% {
      background-position: 0% 54%;
    }
  }
  @-moz-keyframes gradient-slide {
    0% {
      background-position: 0% 54%;
    }
    50% {
      background-position: 100% 47%;
    }
    100% {
      background-position: 0% 54%;
    }
  }
  @keyframes gradient-slide {
    0% {
      background-position: 0% 54%;
    }
    50% {
      background-position: 100% 47%;
    }
    100% {
      background-position: 0% 54%;
    }
  }
  canvas {
    width: 100%;
    height: 100%;
    z-index: -1;
  }
</style>

<article class:red class:blue class:green class:orange class:purple in:fade>
  {#if caseLink}
    <a href={caseLink} target="_blank">Link</a>
  {:else}
    <span class="nolink">No link available</span>
  {/if}
  <h2>{caseTitle}</h2>
  <h3>{caseRole}</h3>
  <img src={caseImage} alt={caseTitle + ' case image'} />
  <h4>Describe it!</h4>
  <p>{caseDescription}</p>
  <h4>What did you do?</h4>
  <p>{caseExecution}</p>
  <h4>Tech?</h4>
  <p>{caseTech}</p>
</article>
